import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Col, Row } from 'reactstrap';
import { ColumnsProps, DefaultSortProps, getExtraData, OrderType, SearchDataGrid, SearchExpression, SearchViewProps, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, InfoComplementarProcessoJudicial } from '../../../../models/dtos/mfp/infoComplementarProcessoJudicial';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'ufVara.sigla',
    order: OrderType.ASC,
  },
];

const InfoComplementarProcessoJudicialSearchView: FC<SearchViewProps<InfoComplementarProcessoJudicial>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<InfoComplementarProcessoJudicial>({ mode: 'onBlur' });

  const columns: ColumnsProps[] = [
    {
      dataField: 'ufVara.sigla',
      sort: true,
      text: 'UF',
    },
    {
      dataField: 'municipio.nome',
      sort: true,
      text: 'Município',
    },
    {
      dataField: 'numeroIdentificacaoVara',
      sort: false,
      text: 'Nº Identificação Vara',
    },
  ];

  const getSearchExpression = () => {
    const searchExpression: SearchExpression[] = [];
    const values: InfoComplementarProcessoJudicial = formProps.getValues();

    if (values.ufVara) {
      searchExpression.push({
        fieldName: 'ufVara.sigla',
        value: values.ufVara.sigla,
      });
    }

    if (values.municipio) {
      searchExpression.push({
        fieldName: 'municipio.codigoMunicipio',
        value: values.municipio.codigoMunicipio,
      });
    }

    return searchExpression;
  };

  return (
    <WindowComponent
      title="Manutenção de Informação Complementar de Processo Judicial"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <Row>
        <Col>
          <CepUFIS id="ufVara" formProps={formProps} />
        </Col>
        <Col>
          <CepLocalidadeIS id="municipio" formProps={formProps} />
        </Col>
      </Row>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        dtoClassName={CLASS_NAME}
        defaultSorted={defaultSorted}
        searchExpressions={getSearchExpression}
        extraData={getExtraData(props, 'InfoComplementarProcessoJudicialEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default InfoComplementarProcessoJudicialSearchView;
